import { createRouter, createWebHistory } from "vue-router";
import Home from "@/pages/Home.vue";

// import About from "@/pages/About.vue";
// import Tel from "@/pages/Tel.vue";
// import Error from "@/pages/404.vue";


export const routers = createRouter({
    history: createWebHistory(),
    routes: [
        { path: '/my', name: 'HomePage', component: Home },
        { path: '/Home', name: 'HomePage', component: Home },
        { path: '/About', name: 'AboutPage', component: () => import('@/pages/About.vue') },
        { path: '/About/:id', name: 'TelPage', component: () => import('@/pages/Tel.vue') },
        { path: '/:catchAll(.*)', name: '404Page', component: () => import('@/pages/404.vue') },
    ],

})


