<template>
    <h1>{{ getTitle }}</h1>

    <div class="addCards">
        <input type="text" placeholder="Введите название бренда">
        <input type="text" placeholder="Введите Название продукта">
        <input type="text" placeholder="Введите описание продукта">
        <input type="text" placeholder="Введите Цену продукта">
        <input type="text" placeholder="Введите адрес картинки для продукта">
        <button>Добавить товар</button>
    </div>

    <div class="main__products">

        <div class="main__products_item" v-for="product in myProduct" :key="product.id">

            <h2 class="main__products_item-title">{{ product.brand }}</h2>

            <img :src="product.images[0]" :alt="product.brand">

            <h3 class="main__products_item-subtitle">Название: {{ product.title }}</h3>
            <p class="main__products_item-price">Цена: {{ product.price }} $</p>
            <p class="main__products_item-text">Описание продукта: {{ product.description }}</p>
        </div>


    </div>
</template>

<script>
export default {


    computed: {
        getTitle() {
            return this.$store.state.title
        },
        myProduct() {
            return this.$store.state.products
        },
        // product100() {
        //     return this.$store.getters.product1000
        // }
    },

    methods: {
        getProduct() {
            this.$store.dispatch('getUsers')
        }
    },

    mounted() {
        this.getProduct()
    },

}
</script>
